@use 'base/variable' as v;
@use 'base/mixins' as m;

.video{
    position: relative;
    max-height: 60rem;
    min-height: 50rem;
    overflow: hidden;

    .overlay{
        position: absolute;
        background: rgb(58,180,160);
        background: linear-gradient(338deg,
        rgba(58,180,160,0.5992254273504274) 0%,
        rgba(29,103,253,0.5963764245014245) 50%,
        rgba(212,69,252,0.5992254273504274) 100%);
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    video{
       width: auto;
       display: block;
       @include m.desktop {
           width: 100%;
       }
    }
}
.contenido-video{
    z-index: 1;
    h2{
        color: white;
        font-size: 6rem;
        margin: 0;
        
    }
    p{
        background-color: rgb(218, 53, 53);
       display: inline-block;
       padding: 1rem 2rem;
       color: white;
       font-weight: 700;
       margin-top: -50px;
       font-size: 2rem;
       margin: 0;

    }
}