html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 86px;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 3rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 2rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  position: relative;
  background-color: #84edb3;
}
@media (min-width: 992px) {
  .header.fijo {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    box-shadow: 0.1rem 0.1rem 0.2rem 0.1rem rgba(0, 0, 0, 0.374);
    animation: navegacion 1s ease-in-out 0s 1 normal forwards;
  }
  @keyframes navegacion {
    0% {
      animation-timing-function: ease-in;
      opacity: 0;
      transform: translateY(-250px);
    }
    38% {
      animation-timing-function: ease-out;
      opacity: 1;
      transform: translateY(0);
    }
    55% {
      animation-timing-function: ease-in;
      transform: translateY(-65px);
    }
    72% {
      animation-timing-function: ease-out;
      transform: translateY(0);
    }
    81% {
      animation-timing-function: ease-in;
      transform: translateY(-28px);
    }
    90% {
      animation-timing-function: ease-out;
      transform: translateY(0);
    }
    95% {
      animation-timing-function: ease-in;
      transform: translateY(-8px);
    }
    100% {
      animation-timing-function: ease-out;
      transform: translateY(0);
    }
  }
}
.header h1 {
  color: #ffffff;
  text-align: center;
  margin: 0;
}
.header .contenido-header {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 1rem;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  color: white;
  font-size: 2.4rem;
  margin-bottom: 1rem;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  color: #fef46d;
}

.video {
  position: relative;
  max-height: 60rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: rgb(58, 180, 160);
  background: linear-gradient(338deg, rgba(58, 180, 160, 0.5992254274) 0%, rgba(29, 103, 253, 0.5963764245) 50%, rgba(212, 69, 252, 0.5992254274) 100%);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 1;
}
.contenido-video h2 {
  color: white;
  font-size: 6rem;
  margin: 0;
}
.contenido-video p {
  background-color: rgb(218, 53, 53);
  display: inline-block;
  padding: 1rem 2rem;
  color: white;
  font-weight: 700;
  margin-top: -50px;
  font-size: 2rem;
  margin: 0;
}

.sobre-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
@media (min-width: 480px) {
  .sobre-festival img {
    margin-bottom: 2rem;
  }
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.contenido-festival h3 {
  margin-bottom: 5rem;
  color: rgb(225, 62, 62);
}
.contenido-festival h2 {
  margin-bottom: 5rem;
}

.barra-uno {
  background-color: rgb(242, 28, 0); /* Old browsers */
  background: -moz-linear-gradient(45deg, rgb(242, 28, 0) 0%, rgb(25, 0, 0) 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(45deg, rgb(242, 28, 0) 0%, rgb(25, 0, 0) 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(45deg, rgb(242, 28, 0) 0%, rgb(25, 0, 0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#f21c00", endColorstr="#190000",GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
  border-bottom: 10px solid black;
}
.barra-uno h3, .barra-uno p {
  color: white;
  text-align: center;
}
.barra-uno .titulo {
  padding-top: 5rem;
  font-size: 10rem;
}
.barra-uno .dia {
  text-transform: uppercase;
  margin-bottom: 5rem 0;
  font-size: 3rem;
}

.escenarios-contenedor {
  padding-bottom: 2rem;
}
@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}
.escenarios-contenedor p {
  color: black;
  font-weight: 700;
  font-size: 25px;
}
.escenarios-contenedor .escenario {
  padding-top: 2rem;
  border-radius: 10px;
}
.escenarios-contenedor .escenario p:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}
.escenarios-contenedor .edm .nombre-escenario:after {
  background-image: url(../img/icono_edm.png);
}
.escenarios-contenedor .rock .nombre-escenario:after {
  background-image: url(../img/icono_rock.png);
}
.escenarios-contenedor .calendario {
  margin: 0;
  padding: 0;
  list-style: none;
  font-weight: 700;
}
.escenarios-contenedor .calendario li {
  padding: 2rem;
}
.escenarios-contenedor .calendario span {
  font-weight: 400;
}
.escenarios-contenedor .bg-amarillo {
  background-color: #84edb3;
}
.escenarios-contenedor .bg-amarillo li:nth-child(odd) {
  background-color: #41e38a;
}
.escenarios-contenedor .bg-verde {
  background-color: #be65ea;
}
.escenarios-contenedor .bg-verde li:nth-child(odd) {
  background-color: #ab38e4;
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
  font-size: 40px;
}

.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.imagen-grande {
  border: 7px solid rgb(11, 10, 11);
}

.btn-cerrar {
  font-size: 30px;
  background-color: rgb(76, 76, 76); /* Old browsers */
  background: -moz-linear-gradient(45deg, rgb(76, 76, 76) 0%, rgb(89, 89, 89) 12%, rgb(102, 102, 102) 25%, rgb(71, 71, 71) 39%, rgb(44, 44, 44) 50%, rgb(0, 0, 0) 51%, rgb(17, 17, 17) 60%, rgb(43, 43, 43) 76%, rgb(28, 28, 28) 91%, rgb(19, 19, 19) 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(45deg, rgb(76, 76, 76) 0%, rgb(89, 89, 89) 12%, rgb(102, 102, 102) 25%, rgb(71, 71, 71) 39%, rgb(44, 44, 44) 50%, rgb(0, 0, 0) 51%, rgb(17, 17, 17) 60%, rgb(43, 43, 43) 76%, rgb(28, 28, 28) 91%, rgb(19, 19, 19) 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(45deg, rgb(76, 76, 76) 0%, rgb(89, 89, 89) 12%, rgb(102, 102, 102) 25%, rgb(71, 71, 71) 39%, rgb(44, 44, 44) 50%, rgb(0, 0, 0) 51%, rgb(17, 17, 17) 60%, rgb(43, 43, 43) 76%, rgb(28, 28, 28) 91%, rgb(19, 19, 19) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#4c4c4c", endColorstr="#131313",GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
  font-weight: 600;
  color: #97ffc6;
  padding: 6px;
  padding-top: 0.05px;
  padding-bottom: 2px;
  border: 4px solid rgb(202, 202, 202);
  display: grid;
  border-radius: 20%;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.boletos {
  background-color: rgb(242, 28, 0); /* Old browsers */
  background: -moz-linear-gradient(45deg, rgb(242, 28, 0) 0%, rgb(25, 0, 0) 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(45deg, rgb(242, 28, 0) 0%, rgb(25, 0, 0) 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(45deg, rgb(242, 28, 0) 0%, rgb(25, 0, 0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#f21c00", endColorstr="#190000",GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
  padding: 4rem;
}
.boletos h3 {
  color: white;
  font-size: 47px;
  text-align: center;
  padding: 2rem;
}
@media (min-width: 768px) {
  .boletos .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}
.boletos .pase-header p, .boletos .pase-footer p {
  text-align: center;
  text-transform: uppercase;
  font-size: 20px;
  color: white;
}
.boletos .pase-header {
  background-color: rgb(112, 216, 159);
  box-shadow: 3px 4px 6px black;
}
.boletos .pase-body {
  box-shadow: 3px 4px 6px black;
  background-color: white;
  line-height: 1rem;
  margin-top: -20px;
  margin-bottom: -20px;
}
.boletos .pase-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.boletos .pase-body ul li, .boletos .pase-body ul :nth-child(odd) {
  background-color: white;
  font-weight: 500;
  padding: 1rem;
  border-bottom: 1px solid rgb(203, 203, 203);
}
.boletos .pase-footer {
  background-color: rgb(0, 72, 255);
  font-weight: 700;
  border-radius: 0 0 5px 5px;
  box-shadow: 3px 4px 9px rgb(0, 0, 0);
}

.tipo {
  color: white;
  text-align: center;
}

.footer {
  width: 100%;
  height: 4rem;
  display: flex;
  background-color: rgb(60, 243, 164);
}
.footer h2 {
  display: flex;
  font-size: 15px;
  margin: 0 auto;
  color: black;
  align-items: flex-end;
  font-weight: 400;
}