@mixin telefono {
    @media (min-width: 480px){
        @content;
    }
}
@mixin tablet {
    @media (min-width: 768px){
        @content;
    }
}
@mixin desktop {
    @media (min-width: 992px){
        @content;
    }
}
@mixin desktopXL {
    @media (min-width: $desktopXL){
        @content;
    }
}
@mixin grid($columnas, $espaciado) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;
}

@mixin resetearLista{
    margin: 0;
    padding:0;
    list-style: none;
}